body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.help-button {
    background-color: white;
    border: 1px;
    color: black;
    padding: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
}

.App-header {
    background-color: white;
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(100px + 2vmin);
    color: black;
}

.Help_page
{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.Help_header
{
    height: 10vh;
    position: -webkit-sticky;
    position: sticky;
}

.Help_text
{
    display: flex;
    flex-direction: column;
    text-align: center;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: -webkit-sticky;
    position: sticky;
    width: 70vw;
    margin-top: 4vh;
}

.Help_sub_header
{
   height: 10vh;
   margin-top: 5vh;
}

#bold
{
    font-weight: bold;
}

@media screen and (max-width: 720px)
{
    .Help_text
    {
        width: 90vw;
    }
}

.Help_return_container
{
    display: flex;
    justify-content: center;
    height: 8vh;
    bottom: 0;
}

.Help_return_button
{
    font-size: 1em;
    margin-right: 1vw;
    padding: 10px 20px;
    transition: 0.3s;
    border: 2px solid rgb(107, 107, 107);
    background-color: white;
}

.Help_return_button:hover
{
    border: 2px solid black;
    background-color: rgb(196, 223, 247);
    transition: 0.3s;
    cursor: pointer;
}
body
{
    font-family: Arial, Helvetica, sans-serif;
    height: 100vh;
    text-align: center;
}

.Login_screen
{
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.Login_container
{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.Login_input_field
{
    font-size: 1.25em;
    width: 20em;
    padding: 10px;
    border: 2px solid rgb(107, 107, 107);
    outline: none;
    transition: 0.5s;
}
.Login_input_field:focus
{
    border: 2px solid black;
}

.Login_button
{
    font-size: 1.25em;
    margin: 10px;
    padding: 16px 32px;
    transition: 0.3s;
    width: 10em;
    border: 2px solid rgb(107, 107, 107);
    background-color: white;
}

.Login_button:hover
{
    border: 2px solid black;
    background-color: rgb(196, 223, 247);
    transition: 0.3s;
    margin: 10px;
    cursor: pointer;
}

.Login_label
{
    font-size: 1.25em;
    margin-bottom: 10px;
}

@media screen and (max-width: 460px)
{
    .Login_input_field
    {
        width: 70vw;
    }
}
